<template>
  <div class="home-con">
    <van-nav-bar title="头条" fixed placeholder />
    <van-pull-refresh
      v-model="refreshing"
      @refresh="onRefresh"
      :disabled="finished"
    >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <ArticleInfo
          v-for="item in artlist"
          :key="item.id"
          :title="item.title"
          :author="item.aut_name"
          :comment="item.comm_count"
          :pubdate="item.pubdate"
          :cover="item.cover"
        ></ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticleAPI } from "@/api/articleAPI.js";
import ArticleInfo from "@/components/Article/ArticleInfo.vue";

export default {
  name: "home",
  data() {
    return {
      page: 1,
      limit: 10,
      artlist: [],
      loading: true,
      finished: false,
      isLoading: false,
      refreshing: false,
    };
  },
  methods: {
    async initArticleList(isRefresh) {
      const { data: res } = await getArticleAPI(this.page, this.limit);
      // console.log(res);
      if (isRefresh) {
        this.artlist = [...res, ...this.artlist];
        this.isLoading = false;
        this.refreshing = false;
      } else {
        this.artlist = [...this.artlist, ...res];
        this.loading = false;
      }
      if (res.length === 0) {
        this.finished = true;
      }
    },
    onLoad() {
      this.page++;
      this.initArticleList();
    },
    onRefresh() {
      console.log("refresh");
      this.page++;
      this.initArticleList(true);
    },
  },
  created() {
    this.initArticleList();
  },
  components: {
    ArticleInfo,
  },
};
</script>

